import React from "react";

import './CurrentUser.scss';
import Axios from "axios";
import Blazy from 'blazy/blazy';
import avatarPlaceholder from '../../../../Shared/images/avatarPlaceholder.png';

export default class CurrentUser extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
          user: {
              name: null,
              email: null,
              photo: null,
          }
        };

        this.isMobile = this.props.isMobile;
    }

    componentDidMount() {
        Axios.get(`https://frontend-test-assignment-api.abz.agency/api/v1/users/1`).then((res) => {
            this.setState({
                user: {
                    name: res.data.user.name,
                    email: res.data.user.email,
                    photo: res.data.user.photo
                }
            });
        });
    }

    render() {
        let bLazy = new Blazy();
        const user = this.state.user;

        return (
            <div className={`current-user${this.isMobile ? ' mobile' : ''}`}>
                <div className={`current-user__info${this.isMobile ? ' mobile' : ''}`}>
                    <div className={`current-user__name${this.isMobile ? ' mobile' : ''}`}>{user.name}</div>
                    <div className={`current-user__email${this.isMobile ? ' mobile' : ''}`}>{user.email}</div>
                </div>
                <div className={`current-user__photo${this.isMobile ? ' mobile' : ''}`}>
                    <img className="b-lazy" src={avatarPlaceholder} data-src={user.photo} alt="currentUser"/>
                </div>
            </div>
        )
    }
}